import { useEffect, useCallback } from 'react';
import soundManager from '../utils/SoundManager';

/**
 *       
 */
export const useSound = () => {
  //  AudioContext    
  useEffect(() => {
    const handleFirstInteraction = () => {
      if (soundManager.context && soundManager.context.state === 'suspended') {
        soundManager.context.resume();
      }
      //     
      document.removeEventListener('click', handleFirstInteraction);
      document.removeEventListener('keydown', handleFirstInteraction);
    };

    document.addEventListener('click', handleFirstInteraction);
    document.addEventListener('keydown', handleFirstInteraction);

    return () => {
      document.removeEventListener('click', handleFirstInteraction);
      document.removeEventListener('keydown', handleFirstInteraction);
    };
  }, []);

  //      
  const gameAudio = {
    createBet: useCallback((gameType = 'ALL') => soundManager.playSound('_', gameType), []),
    acceptBet: useCallback((gameType = 'ALL') => soundManager.playSound('_', gameType), []),
    selectMove: useCallback((gameType = 'ALL') => soundManager.playSound('_', gameType), []),
    reveal: useCallback((gameType = 'ALL') => soundManager.playSound('reveal', gameType), []),
    victory: useCallback((gameType = 'ALL') => soundManager.playSound('', gameType), []),
    defeat: useCallback((gameType = 'ALL') => soundManager.playSound('', gameType), []),
    draw: useCallback((gameType = 'ALL') => soundManager.playSound('', gameType), [])
  };

  //    
  const gemAudio = {
    buy: useCallback(() => soundManager.playSound('_'), []),
    sell: useCallback(() => soundManager.playSound('_'), []),
    gift: useCallback(() => soundManager.playSound('_'), [])
  };

  //  
  const uiAudio = {
    hover: useCallback(() => soundManager.playSound('hover'), []),
    modalOpen: useCallback(() => soundManager.playSound('_'), []),
    modalClose: useCallback(() => soundManager.playSound('_'), []),
    notification: useCallback(() => soundManager.playSound(''), []),
    error: useCallback(() => soundManager.playSound(''), []),
    click: useCallback(() => soundManager.playSound('_'), []) //      
  };

  //  
  const systemAudio = {
    timerExpire: useCallback(() => soundManager.playSound('_reveal'), []),
    reward: useCallback(() => soundManager.playSound(''), [])
  };

  //  
  const soundSettings = {
    isEnabled: useCallback(() => soundManager.enabled, []),
    getVolume: useCallback(() => soundManager.volume, []),
    setEnabled: useCallback((enabled) => soundManager.updateSettings(enabled, soundManager.volume), []),
    setVolume: useCallback((volume) => soundManager.updateSettings(soundManager.enabled, volume), []),
    reloadSounds: useCallback(() => soundManager.reloadSounds(), [])
  };

  //    API
  const playSound = {
    createBet: gameAudio.createBet,
    acceptBet: gameAudio.acceptBet,
    selectMove: gameAudio.selectMove,
    reveal: gameAudio.reveal,
    victory: gameAudio.victory,
    defeat: gameAudio.defeat,
    draw: gameAudio.draw,
    buyGem: gemAudio.buy,
    sellGem: gemAudio.sell,
    giftGem: gemAudio.gift,
    hover: uiAudio.hover,
    modalOpen: uiAudio.modalOpen,
    modalClose: uiAudio.modalClose,
    notification: uiAudio.notification,
    error: uiAudio.error,
    settings: uiAudio.click,
    timerExpire: systemAudio.timerExpire,
    reward: systemAudio.reward
  };

  return {
    //  
    game: gameAudio,
    gem: gemAudio,
    ui: uiAudio,
    system: systemAudio,
    
    // 
    settings: soundSettings,
    
    //    playSound  
    playSound,
    
    //  
    playCoin: gemAudio.buy, //   
    playClick: uiAudio.click,
    playSuccess: gameAudio.victory,
    playModalClose: uiAudio.modalClose,
    playHover: uiAudio.hover
  };
};

/**
 *         
 */
export const useHoverSound = (enabled = true) => {
  const { ui } = useSound();

  const hoverProps = enabled ? {
    onMouseEnter: ui.hover
  } : {};

  return hoverProps;
};

/**
 *     
 */
export const useModalSound = () => {
  const { ui } = useSound();

  return {
    onOpen: ui.modalOpen,
    onClose: ui.modalClose
  };
};

/**
 *        
 */
export const useGameSound = (gameResult) => {
  const { game } = useSound();

  useEffect(() => {
    if (gameResult) {
      switch (gameResult.toLowerCase()) {
        case 'win':
        case 'victory':
          game.victory();
          break;
        case 'lose':
        case 'defeat':
          game.defeat();
          break;
        case 'draw':
        case 'tie':
          game.draw();
          break;
      }
    }
  }, [gameResult, game]);

  return game;
};